</el-icon>\r\n          {{ article.likesCount }}\r\n        </span>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\nimport { useRouter } from 'vue-router';\r\n\r\nconst props = defineProps({\r\n  article: {\r\n    type: Object,\r\n    required: true\r\n  }\r\n});\r\n\r\nconst router = useRouter();\r\n\r\nconst goToArticle = () => {\r\n  router.push(`/article/${props.article.id}`);\r\n};\r\n\r\nconst formatDate = (date) => {\r\n  if (!date) return '';\r\n  return new Date(date).toLocaleDateString('zh-CN', {\r\n    year: 'numeric',\r\n    month: 'long',\r\n    day: 'numeric'\r\n  });\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.article-card {\r\n  cursor: pointer;\r\n  height: 100%;\r\n  display: flex;\r\n  flex-direction: column;\r\n  transition: all var(--transition-fast);\r\n}\r\n\r\n.article-card:hover {\r\n  border-color: var(--color-primary);\r\n}\r\n\r\n.article-header {\r\n  margin-bottom: var(--spacing-md);\r\n}\r\n\r\n.article-title {\r\n  font-size: 1.5rem;\r\n  margin-bottom: var(--spacing-sm);\r\n  color: var(--color-text-primary);\r\n  line-height: 1.4;\r\n}\r\n\r\n.article-meta {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-md);\r\n  color: var(--color-text-secondary);\r\n  font-size: 0.9rem;\r\n}\r\n\r\n.author-link {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-sm);\r\n  color: var(--color-text-secondary);\r\n  transition: color var(--transition-fast);\r\n}\r\n\r\n.author-link:hover {\r\n  color: var(--color-primary);\r\n}\r\n\r\n.author-avatar {\r\n  width: 24px;\r\n  height: 24px;\r\n  border-radius: 50%;\r\n}\r\n\r\n.article-excerpt {\r\n  flex: 1;\r\n  color: var(--color-text-secondary);\r\n  line-height: 1.6;\r\n  margin-bottom: var(--spacing-md);\r\n}\r\n\r\n.article-footer {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding-top: var(--spacing-md);\r\n  border-top: 1px solid var(--color-border);\r\n}\r\n\r\n.tags {\r\