.profile-banner[data-v-d9aa1c26] {
  width: 100%;
  height: 300px;
  background-size: cover;
  background-position: center;
  position: relative;
}
.banner-overlay[data-v-d9aa1c26] {
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom, transparent, var(--color-bg-base));
}
.profile-header[data-v-d9aa1c26] {
  display: flex;
  align-items: center;
  gap: var(--spacing-lg);
  margin-top: -80px;
  margin-bottom: var(--spacing-2xl);
  position: relative;
  z-index: 10;
}
.profile-avatar[data-v-d9aa1c26] {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 6px solid var(--color-bg-base);
  box-shadow: var(--shadow-lg);
}
.profile-info h1[data-v-d9aa1c26] {
  font-size: 2.5rem;
  margin-bottom: var(--spacing-sm);
}
.user-role[data-v-d9aa1c26] {
  color: var(--color-text-muted);
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1px;
}
.profile-bio[data-v-d9aa1c26] {
  margin-bottom: var(--spacing-2xl);
}
.profile-bio h3[data-v-d9aa1c26] {
  margin-bottom: var(--spacing-md);
}
.bio-content[data-v-d9aa1c26] {
  color: var(--color-text-secondary);
  line-height: 1.8;
  min-height: 60px;
}
.bio-edit-section[data-v-d9aa1c26] {
  margin-top: var(--spacing-lg);
