      </div>\r\n        \r\n        <!-- 用户文章列表 -->\r\n        <div class=\"user-articles\">\r\n          <h2>发布的文章 ({{ articles.length }})</h2>\r\n          <div class=\"articles-grid\">\r\n            <article-card v-for=\"article in articles\" :key=\"article.id\" :article=\"article\" />\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n    \r\n    <div v-else-if=\"loading\" class=\"loading\">\r\n      <div class=\"spinner\"></div>\r\n    </div>\r\n    \r\n    <!-- 编辑个人简介弹窗 -->\r\n    <el-dialog v-model=\"showEditDialog\" title=\"编辑个人简介\" width=\"600px\">\r\n      <el-input\r\n        v-model=\"bioContent\"\r\n        type=\"textarea\"\r\n        :rows=\"6\"\r\n        placeholder=\"支持HTML格式（用于XSS演示）\"\r\n      />\r\n      <template #footer>\r\n        <el-button @click=\"showEditDialog = false\">取消</el-button>\r\n        <el-button type=\"primary\" @click=\"updateBio\" :loading=\"saving\">保存</el-button>\r\n      </template>\r\n    </el-dialog>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\n// 个人主页（场景 4）：演示存储型 XSS（伪造登录页面）\r\n// - VULN：user.bio 原样渲染（可能执行恶意脚本）\r\n// - SECURE：使用 DOMPurify 净化后再渲染\r\nimport { ref, computed, onMounted } from 'vue';\r\nimport { useRoute } from 'vue-router';\r\nimport { useAuthStore } from '@/stores/auth';\r\nimport { useConfigStore } from '@/stores/config';\r\nimport { pure } from '@/utils/xss';\r\nimport axios from '@/api/axios';\r\nimport ArticleCard from '@/components/ArticleCard.vue';\r\nimport { ElMessage } from 'element-plus';\r\n\r\nconst route = useRoute();\r\nconst authStore = useAuthStore();\r\nconst configStore = useConfigStore();\r\n\r\nconst user = ref(null);\r\nconst articles = ref([]);\r\nconst loading = ref(true);\r\nconst showEditDialog = ref(false);\r\nconst bioContent = ref('');\r\nconst saving = ref(false);\r\n\r\nconst isOwnProfile = computed(() => {\r\n  return authStore.isAuthenticated && authStore.user?.username === route.params.username;\r\n});\r\n\r\nconst fetchProfile = async () => {\r\n  try {\r\n    const response = await axios.get(`/profile/${route.params.username}`);\r\n    user.value = response.data.user;\r\n    articles.value = response.data.articles.content || [];\r\n    bioContent.value = user.value.bio || '';\r\n  } catch (error) {\r\n    console.error('Failed to fetch profile:', error);\r\n  } finally {\r\n    loading.value = false;\r\n  }\r\n};\r\n\r\nconst updateBio = async () => {\r\n  saving.value = true;\r\n  try {\r\n    await axios.post('/profile/bio', { bio: bioContent.value });\r\n    user.value.bio = bioContent.value;\r\n    showEditDialog.value = false;\r\n    ElMessage.success('简介已更新');\r\n  } catch (error) {\r\n    ElMessage.error('更新失败');\r\n  } finally {\r\n    saving.value = false;\r\n  }\r\n};\r\n\r\nonMounted(() => {\r\n  fetchProfile();\r\n});\r\n</script>\r\n\r\n<style scoped>\r\n.profile-banner {\r\n  width: 100%;\r\n  height: 300px;\r\n  background-size: cover;\r\n  background-position: center;\r\n  position: relative;\r\n}\r\n\r\n.banner-overlay {\r\n  position: absolute;\r\n  inset: 0;\r\n  background: linear-gradient(to bottom, transparent, var(--color-bg-base));\r\n}\r\n\r\n.profile-header {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-lg);\r\n  margin-top: -80px;\r\n  margin-bottom: var(--spacing-2xl);\r\n  position: relative;\r\n  z-index: 10;\r\n}\r\n\r\n.profile-avatar {\r\n  width: 160px;\r\n  height: 160px;\r\n  border-radius: 50%;\r\n  border: 6px solid var(--color-bg-base);\r\n  box-shadow: var(--shadow-lg);\r\n}\r\n\r\n.profile-info h1 {\r\n  font-size: 2.5rem;\r\n  margin-bottom: var(--spacing-sm);\r\n}\r\n\r\n.user-role {\r\n  color: var(--color-text-muted);\r\n  text-transform: uppercase;\r\n  font-weight: 600;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.profile-bio {\r\n  margin-bottom: var(--spacing-2xl);\r\n}\r\n\r\n.profile-bio h3 {\r\n  margin-bottom: var(--spacing-md);\r\n}\r\n\r\n.bio-content {\r\n  color: var(--color-text-secondary);\r\n  line-height: 1.8;\r\n  min-height: 60px;\r\n}\r\n\r\n.bio-edit-section {\r\n  margin-top: var(--spacing-lg);\r\n  padding-top: var(--spacing-lg);\r\n  border-top: 1px solid var(--color-border);\r\n}\r\n\r\n.user-articles h2 {\r\n  margin-bottom: var(--spacing-xl);\r\n}\r\n\r\n.articles-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));\r\n  gap: var(--spacing-lg);\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .profile-header {\r\n    flex-direction: column;\r\n    text-align: center;\r\n  }\r\n  \r\n  .articles-grid {\r\n    grid-template-columns: 1fr;\r\n  }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
